/* App.css */

.body {
  background-color: #3498db; /* Background color for the entire page */
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
  padding: 20px;
  background-color: #ffffff; /* Background color for the main content area */
  border-radius: 10px; /* Add rounded corners to the content area */
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2); /* Add a shadow to the content area */
}

h1 {
  color: #34495e; /* Header text color */
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

label {
  margin-top: 10px;
  font-weight: bold;
  color: #333;
}

input[type="file"] {
  margin-bottom: 10px;
}

button {
  background-color: #e74c3c; /* Button background color */
  color: #fff;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
}

button:hover {
  background-color: #c0392b; /* Button background color on hover */
}

h2 {
  margin-top: 20px;
  font-weight: bold;
  color: #333;
}
